@import url(https://fonts.googleapis.com/css2?family=Montserrat&display=swap);
html, body, div, form, fieldset, legend, label {
  margin: 0;
  padding: 0; 
}

body {
  font-family: 'Montserrat', sans-serif;
}

table {
  border-collapse: collapse;
  border-spacing: 0;
}

th, td {
  text-align: left;
  vertical-align: top;
}

h1, h2, h3, h4, h5, h6, th, td, caption { font-weight:normal; }

img { border: 0; }

.app {
  display: flex;
  flex-direction: row;
  justify-content: flex-start;
}

.form {
  padding: 1rem;
  min-width: 400px;
}

label {
  display: block;
  padding: .5rem 0;
}

label > span {
  display: inline-block;
  width: 120px;
  text-align: right;
  margin-right: .5rem;
}

.actions {
  display: flex;
  flex-direction: row;
  margin-top: 1rem;
}

.actions * {
  flex-grow: 1;
}

.form-field {
  padding: .3rem;
  border-radius: 4px;
  border: 1px solid #999;
}

button {
  padding: .6rem 0;
  text-transform: uppercase;
  background-color: #ddd;
  border: 1px solid #999;
  border-radius: .3rem;
  margin: 0 .3rem;
  font-weight: 600;
}

.app-name {
  margin-top: 0;
}

.container {
  border: .5mm solid #999;
  margin: 1rem 0;
}

.viewport {
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  overflow: hidden;
  width: 190mm;
  height: 275mm;
  border: 10mm solid #fff;
}

.viewport.no-spread {
  justify-content: center;
  align-content: center;
}

.viewport.spread {
  justify-content: space-evenly;
  align-items: center;
}

.item {
  position: relative;
  display: inline-flex;
  width: var(--diameter);
  height: var(--diameter);
  border: .5mm solid black;
  overflow: hidden;
  background-repeat: no-repeat;
  background-size: 100% 100%;
  background-position: center center;
}

.viewport.no-border .item,
.viewport.shape .item {
  border: none !important;
  background-size: calc(100% - 2px) calc(100% - 2px);
}

.viewport.shape .item::before {
  content: " ";
  background-repeat: no-repeat;
  background-position: center center;
  background-size: 100% 100%;
  position: absolute;
  width: calc(100% + 2px);
  height: calc(100% + 2px);
  top: -1px;
  left: -1px;
  /* bottom: -1px;
  right: -1px; */
  z-index: 2;
}

.viewport.shape .item::after {
  content: " ";
  border: 2px solid #fff;
  position: absolute;
  width: 100%;
  height: 100%;
  top: -1px;
  left: -1px;
  /* bottom: -1px;
  right: -1px; */
  z-index: 1;
}

.viewport.square.no-spread .item {
  margin: -.5mm 0 0 -.5mm;
}

.viewport.square.no-spread.no-border .item {
  margin: 0 !important;
}

.viewport.circle .item {
  border-radius: 50%;
}

.viewport.shape.star .item::before {
  background-image: url(/papelab/static/media/star.border.51151376.svg);
}
.viewport.shape.star.no-border .item::before {
  background-image: url(/papelab/static/media/star.5b41c36b.svg);
}

.viewport.shape.heart .item::before {
  background-image: url(/papelab/static/media/heart.border.46adb08b.svg);
}
.viewport.shape.heart.no-border .item::before {
  background-image: url(/papelab/static/media/heart.bfbff3de.svg);
}

@media print {
  @page {
      size: A4;
      margin: 0;
  }
  * {
      -webkit-print-color-adjust: exact !important;
      color-adjust: exact !important;
  }
  html, body {
      width: 210mm;
      height: 296mm;
      overflow: hidden;
  }
  .form {
      display: none;
  }
  .container {
      border: none !important;
      margin: 0 !important;
  }
}

